import React,{useState} from 'react';

const numbers=[
    {id:1, value:8},
    {id:2, value:100},
    {id:3, value:24},
    {id:4, value:5},
];

function Memorize(){
    const memArr=[...numbers, ...numbers];
    const [compare, setCompare]=useState([]);
    const [disable, setDisable]=useState(false);
    const [clicked,setClicked]=useState("");

    React.useEffect(()=>{
        if(compare.length ===2){
            setDisable(true);
        }
    },[compare]);

    const handleCard=idCard=>{
        if(!disable){
            setCompare([...compare,idCard]);
        }

        let divFront=document.getElementById("front_"+idCard);
        let divBack=document.getElementById("back_"+idCard);

        divFront.classList.add("front_hide");
        divBack.classList.add("back_show");
    }
    return(
        <div className="app">
            {console.log(compare)}
            {
                memArr.map((val,idx)=>{
                    return <div key={idx} id={val.id} 
                            className="card " 
                            onClick={e=>handleCard(val.id)} 
                            disabled={(disable)?true:false}
                            >
                            <section className="front" id={`front_${val.id}`}>
                            </section>
                            <section className="back" id={`back_${val.id}`}>
                                <h2>{val.value}</h2>
                            </section>
                        </div>
                })
            }
        </div>
    )
}

export default Memorize;